import React, {Component} from 'react';
import {Helmet} from 'react-helmet';
import Snake from './Snake';
import Food from './Food';

const getRandomCoordinates = () => {
  let min = 1;
  let max = 98;
  let x = Math.floor((Math.random()*(max-min+1)+min)/2)*2;
  let y = Math.floor((Math.random()*(max-min+1)+min)/2)*2;
  return [x,y]
}

const initialState = {
  food: getRandomCoordinates(),
  speed: 200,
  direction: 'RIGHT',
  snakeDots: [
    [0,0],
    [2,0]
  ],
}

class Javasnake extends Component {
  state = initialState;

  componentDidMount() {
    setInterval(this.moveSnake, this.state.speed);
    document.onkeydown = this.onkeyDown;
  }
  componentDidUpdate(){
    this.checkIfOutOfBorders();
    this.checkIfCollapsed();
    this.checkIfEat();
  }

  onkeyDown = (e) => {
    e = e || window.event;
    switch (e.keyCode) {
      case 38:
        this.setState({direction: 'UP'});
        break;
      case 40:
        this.setState({direction: 'DOWN'});
        break;
      case 37:
        this.setState({direction: 'LEFT'});
        break;
      case 39:
        this.setState({direction: 'RIGHT'});
        break;
      default:
          break;
    }
  }

  moveSnake = () => {
    let dots = [...this.state.snakeDots];
    let head = dots[dots.length - 1];

    switch (this.state.direction) {
      case 'RIGHT':
        head = [head[0] + 2, head[1]];
        break;
      case 'LEFT':
        head = [head[0] - 2, head[1]];
        break;
      case 'DOWN':
        head = [head[0], head[1] + 2];
        break;
      case 'UP':
        head = [head[0], head[1] - 2];
        break;
      default:
          break;
    }
    dots.push(head);
    dots.shift();
    this.setState({
      snakeDots: dots
    })
  }

  checkIfOutOfBorders() {
    let head = this.state.snakeDots[this.state.snakeDots.length - 1];
    if (head[0] >= 100 || head[1] >= 100 || head[0] < 0 || head[1] < 0) {
      this.onGameOver();
    }
  }

  checkIfCollapsed(){
    let snake = [...this.state.snakeDots];
    let head = snake[snake.length -1];
    snake.pop();
    snake.forEach(dot => {
      if (head[0] === dot[0] && head[1] === dot[1]) {
        this.onGameOver();
      }
    });
  }

  checkIfEat() {
    let head = this.state.snakeDots[this.state.snakeDots.length - 1];
    let food = this.state.food;
    if (head[0] === food[0] && head[1] === food[1]){
      this.setState({
        food: getRandomCoordinates()
      })
      this.enlargeSnake();
      //this.increaseSpeed();
      if (this.state.speed < 10) {
        this.setState({
          speed: this.state.speed += 500
        })
      }
      console.log('speed', this.state.speed);
    }
  }

  enlargeSnake() {
    let newSnake = [...this.state.snakeDots];
    newSnake.unshift([]);
    this.setState({
      snakeDots: newSnake
    })
  }

  increaseSpeed() {
    if (this.state.speed < 10) {
      this.setState({
        speed: this.state.speed - 1000
      })
    }
  }

  handlePause(){
    if (this.state.speed > 10) {
      this.setState({
        speed: this.state.speed + 1000
      })
    }
  }
  handlePlay(){
    if (this.state.speed > 10) {
      this.setState({
        speed: this.state.speed - 1000
      })
    }
  }

  onGameOver() {
    alert(`Game Over, Snake length is ${this.state.snakeDots.length}`);
    this.setState(initialState);
  }

  render() {
    return (
      <div className="js-background">
        <Helmet><title>YGBC- JavaSnake</title></Helmet>
        <div style={{display: 'flex', justifyContent: 'center'}}>
          <div><h1>JavaSnake</h1></div>
          <div>
            <button id="js-pause" onClick={this.handlePause()}>
              PAUSE
            </button>
          </div>
          <div>
            <button id="js-play-button" onClick={this.handlePlay()}>
              PLAY
            </button>
          </div>
        </div>
        <div className="js-game-area">
          <Snake snakeDots={this.state.snakeDots}/>
          <Food dot={this.state.food} />
        </div>
      </div>
    );
  }
}

export default Javasnake;
